'use client';

import { useState } from 'react';
import Link from 'next/link';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faTachometerAlt, faShoppingCart, faList, faUser, faMoneyBillAlt, faUserSlash, faUserPlus, faComment, faSignOutAlt } from '@fortawesome/free-solid-svg-icons';

const Sidebar = () => {
  const [isOpen, setIsOpen] = useState(false);  // State to toggle sidebar visibility
  const [isHamburger, setIsHamburger] = useState(true);  // State to manage hamburger icon

  const handleToggle = () => {
    setIsOpen(!isOpen);
    setIsHamburger(!isHamburger);
  };

  return (
    <div>
      {/* Sidebar */}
      <div
        className={`${
          isOpen ? 'w-64' : 'w-0'
        } md:w-64 bg-slate-200 text-white p-4 fixed top-0 left-0 bottom-0 z-10 transition-all duration-300 transform ease-in-out md:relative md:block`}
      >
        {/* X Button to close sidebar */}
        <button
          className="absolute top-4 right-4 text-2xl text-gray-800 md:hidden"
          onClick={handleToggle}
        >
          <FontAwesomeIcon icon="times" />
        </button>

        {/* Sidebar Logo */}
        <div className="logo mb-4">
          <img src="/assets/logo.png" alt="Logo" className="w-72 h-54 mx-auto pointer-events-none" />
        </div>

        {/* Sidebar Menu */}
        <ul>
          <li>
            <Link href="/admin/dashboard" className="flex items-center py-2 pl-4 text-blue-950 hover:bg-blue-500">
              <FontAwesomeIcon icon={faTachometerAlt} className="mr-2 text-blue-950" />
              {isOpen && <span>Dashboard</span>}
            </Link>
          </li>
          <li>
            <Link href="/admin/orders" className="flex items-center py-2 pl-4 text-blue-950 hover:bg-gray-700">
              <FontAwesomeIcon icon={faShoppingCart} className="mr-2 text-blue-950" />
              {isOpen && <span>Orders</span>}
            </Link>
          </li>
          <li>
            <Link href="/category" className="flex items-center py-2 pl-4 hover:bg-gray-700 text-blue-950">
              <FontAwesomeIcon icon={faList} className="mr-2 text-blue-950" />
              {isOpen && <span>Category</span>}
            </Link>
          </li>
          <li>
            <Link href="/sellers" className="flex items-center py-2 pl-4 text-blue-950 hover:bg-gray-700">
              <FontAwesomeIcon icon={faUser} className="mr-2 text-blue-950" />
              {isOpen && <span>Sellers</span>}
            </Link>
          </li>
          <li>
            <Link href="/payment-request" className="flex items-center py-2 pl-4 hover:bg-gray-700 text-blue-950">
              <FontAwesomeIcon icon={faMoneyBillAlt} className="mr-2 text-blue-950" />
              {isOpen && <span>Payment Request</span>}
            </Link>
          </li>
          <li>
            <Link href="/deactive-sellers" className="flex items-center py-2 pl-4 hover:bg-gray-700 text-blue-950">
              <FontAwesomeIcon icon={faUserSlash} className="mr-2 text-blue-950" />
              {isOpen && <span>Deactive Sellers</span>}
            </Link>
          </li>
          <li>
            <Link href="/seller-request" className="flex items-center py-2 pl-4 hover:bg-gray-700 text-blue-950">
              <FontAwesomeIcon icon={faUserPlus} className="mr-2 text-blue-950" />
              {isOpen && <span>Seller Request</span>}
            </Link>
          </li>
          <li>
            <Link href="/live-chat" className="flex items-center py-2 pl-4 hover:bg-gray-700 text-blue-950">
              <FontAwesomeIcon icon={faComment} className="mr-2 text-blue-950" />
              {isOpen && <span>Live Chat</span>}
            </Link>
          </li>
          <li>
            <Link href="/logout" className="flex items-center py-2 pl-4 hover:bg-gray-700 text-blue-950">
              <FontAwesomeIcon icon={faSignOutAlt} className="mr-2 text-blue-950" />
              {isOpen && <span>Logout</span>}
            </Link>
          </li>
        </ul>
      </div>

      {/* Main Content Area */}
      <div className={`flex-1 p-4 md:p-6 ${isOpen ? 'ml-64' : ''}`}>
        {/* Content here */}
      </div>

      {/* Hamburger Icon for mobile */}
      <button
        onClick={handleToggle}
        className={`md:hidden p-4 fixed top-4 left-4 z-20 ${isHamburger ? 'bg-blue-500' : 'bg-transparent'} transition-all`}
      >
        <FontAwesomeIcon icon={isHamburger ? 'bars' : 'times'} className="text-white text-2xl" />
      </button>
    </div>
  );
};

export default Sidebar;
